@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap");

.App {
  background-color: #63607d;
  padding: 7vw 0;
  height: 100%;
  display: flex;
  flex-direction: column;  
  justify-content: center;
  align-items: center;
  gap: 6vw;
}

.Gengar {
  position: absolute;
  opacity: 0.6;
  bottom: 10px;
  left: 40px;
  width: 120px;
}

.Puntos {
  position: absolute;
  right: 0;
  top: 0;
  width: 350px;
}

.Micro {
  position: absolute;
  width: 120px;
  right: 50px;
  bottom: 50px;
}
/*?==============
/*?     PAPEL 
/*?==============*/ 
.Papel::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 30px, /*? Altura de la línea de separación */
    rgba(140, 82, 255, 0.15) 30px, /*? Color de la línea morada con opacidad */
    rgba(140, 82, 255, 0.15) 32px /*? Espaciado entre las líneas */
  );
}

.Papel {
  font-family: "Nunito", sans-serif;
  border-radius: 15px;
  height: 20vw; 
  min-width: 40vw; /*? Ancho mínimo para el contenedor */
  background-color: white;
  position: relative;
  display: inline-block; 
  margin: 1vw auto; 
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
}

.Papel h1{
  padding: 0 50px;
  font-size: 5vw;
  font-weight: 700;
  margin: 0;
  height: 100%;
  
  display: flex;
  align-items: center;
  justify-content: center;
}

.contador {
  position: absolute;
  left: 0px;
  top: 0px;
  color: darkgray;
  border-radius: 50%;
  padding-left: 20px;
  font-size: 25px;
}
/*?==============
/*?  RESPUESTA 
/*?==============*/ 
.Respuesta {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  background-color: #c3beed;
  border-radius: 50px;
  padding: 10px 20px ;

  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 56vw;
}

#Atomo_img {
  width: 60px;
}

#Continuar_img {
  width: 80px;
}

.Respuesta input::placeholder {
  font-family: "Nunito", sans-serif;
  text-align: left;
  font-weight:300;
}

.Respuesta input {
  margin: 0 10px;
  font-family: "Nunito", sans-serif;
  text-align: left;
  caret-color: purple; /*? Cambia el color del caret */

  font-size: 3vw;
  font-weight: 600;
  background-color: #c3beed;

  width: 45vw;
  border: none;
}

.Respuesta input:focus {
  outline: none; 
}